@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-size: 16px;
  --global-text-color: #fff;
  --global-margin-top: 5vh;
}

body {
  font-family: Nunito Sans, sans-serif;
  color: #fff;
  color: var(--global-text-color);
  font-size: calc(.25em + 1.5vw);
}

ul {
  list-style-type: none;
}

a {
  color: #000;
  text-decoration: none;
  font-size: 1.5vw;
}

.container {
  max-width: 1180px;
  padding: 0 15px;
  margin: 0 auto;
}

.content {
  height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  background-size: cover;
}

.navigation ul {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.navigation .the-only-one {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.navigation .the-only-one .link-to {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}

.navigation ul li {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.notfound {
  color: #000;
}

@media (max-width: 550px) {
  body {
    font-size: calc(.25em + 2.75vw);
  }
  
  .slide2 .content .wrap {
    font-size: calc(3vw);
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  
  .slide2 .content .wrap .beginning {
    width: 85%;
  }
  
  .slide2 .content .wrap .end {
    width: 85%;
  }

  .slide2 .content .end .text {
    margin-top: 5vh;
  }
  
  .slide2 .content .end .walls {
    display: none;
  }
  
  .slide3 .content {
    font-size: 4vw;
  }
  
  .slide3 .content .beginning {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .slide3 .content .container .beginning .text {
    margin: 0;
    width: 85%;
  }
  
  .slide3 .content .end .platooning {
    display: none;
  }

  .slide3 .content .container .end .text {
    width: 85%;
  }

  .slide4 .content {
    font-size: calc(2.5vw);
  }

  .slide6 .content {
    font-size: 2.5vw;
  }
}
.introduction .content {
    background-image: url(/presentation_2021/static/media/codeBG.a66bb638.jpg);
}

.introduction .content {
    display: block;
}

.introduction .content .container {
    padding-top: 5vh;
}

.introduction .content .topic {
    font-size: 4vw;
}

.introduction .content .creators {
    text-align: right;
    margin-top: 20vh;
}

.introduction .content .creators .creator {
    display: block;
}

.introduction .content .creators .creator + .creator {
    margin-top: 1vh;
}

.slide1 .content {
    background-image: url(/presentation_2021/static/media/presentationBG.c81ebe93.jpg);
}

.slide1 .content .beginning {
    margin-top: 5vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
}

.slide1 .content .beginning .li-fi {
    width: 30%;
}

.slide1 .content .beginning .text {
    width: 50%;
}

.slide1 .content .end .text {
    margin-top: var(--global-margin-top);
}

.slide2 .content {
    background-image: url(/presentation_2021/static/media/presentationBG2.6c177efe.jpg);
}

.slide2 .content h2 {
    text-align: center;
}

.slide2 .content .container .wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin-top: var(--global-margin-top);
}

.slide2 .content .beginning {
    width: 50%;
}

.slide2 .content .end {
    width: 40%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.slide2 .content .end .walls {
    width: 75%;
    margin: 0 auto;
}

.slide2 .content .end .text {
    margin-top: 15vh;
    text-align: right;
}

.slide3 .content {
    background-image: url(/presentation_2021/static/media/presentationBG.c81ebe93.jpg);
}

.slide3 .content h2 {
    text-align: right;
}

.slide3 .content .beginning {
    margin-top: 10vh;
}

.slide3 .content .beginning .text {
    width: 55%;
    margin: 0 auto;
    text-align: right;
}

.slide3 .content .end {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    margin-top: var(--global-margin-top);
}

.slide3 .content .end .platooning {
    width: 35%;
}

.slide3 .content .end .text {
    width: 55%;
}

.slide4 .content {
    background-image: url(/presentation_2021/static/media/presentationBG3.72e84a0c.jpg);
}

.slide4 .content .wrap {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 10vh;
}

.slide4 .content .beginning {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.slide4 .content .beginning .text:first-child {
    text-align: center;
}

.slide4 .content .beginning .text:nth-child(2) {
    width: 50%;
    margin: 0 auto;
}

.slide4 .content .beginning .text + .text {
    margin-top: var(--global-margin-top);
}

.slide5 .content .plastic-barriers {
    width: 100%;
    height: 100vh;
}

.slide6 .content {
    background-image: url(/presentation_2021/static/media/endBG.3dc46833.jpg);
}

.slide6 .content .text {
    height: 70vh;
}

.slide6 .content h2 {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}
